const state = {
  // 
  arr: [
    'l1',
    'l2',
    'l3',
    'l4',
    'l5',
    'l6'
  ],
  //  = 
  levelToShow: {
    l1: '',
    l2: '',
    l3: '',
    l4: '',
    l5: '',
    l6: ''
  },
  //  = 
  levelToSimpleShow: {
    l1: '',
    l2: '',
    l3: '',
    l4: '',
    l5: '',
    l6: ''
  },
  //  = 
  levelToColors: {
    l1: '#43ce17',
    l2: '#efdc31',
    l3: '#ffaa00',
    l4: '#ff401a',
    l5: '#d20040',
    l6: '#9c0a4e'
  },
  //  = 
  levelToFontColors: {
    l1: '#282828',
    l2: '#282828',
    l3: '#ffffff',
    l4: '#ffffff',
    l5: '#ffffff',
    l6: '#ffffff'
  },
  //  = 
  pollToLvlLine: {
    so2: [150, 500, 650, 800],
    no2: [100, 200, 700, 1200, 2340, 3090, 3840],
    pm10: [50, 150, 250, 350, 420, 500, 600],
    pm25: [35, 75, 115, 150, 250, 350, 500],
    o3: [160, 200, 300, 400, 800, 1000, 1200],
    co: [5, 10, 35, 60, 90, 120, 150],
    aqi: [50, 100, 150, 200, 300, 500],
    tvoc: []
  }
}

const getters = {
  // /**
  //  * @desc 
  //  * @param {String} lvl 
  //  * @returns {String} 
  //  */
  getColorByLevel: state => lvl => state.levelToColors[lvl],
  // /**
  //  * @desc 
  //  * @param {String} lvl 
  //  * @returns {String} 
  //  */
  getFontColorByLevel: state => lvl => state.levelToFontColors[lvl],
  // /**
  //  * @desc 
  //  * @param {String} lvl 
  //  * @returns {String} 
  //  */
  getShowByLevel: state => lvl => state.levelToShow[lvl],
  // /**
  //  * @desc 
  //  * @param {String} lvl 
  //  * @returns {String} 
  //  */
  getSimpleShowByLevel: state => lvl => state.levelToSimpleShow[lvl],
  // /**
  //  * @desc 
  //  * @param {String} poll 
  //  * @returns {String} 
  //  */
  getMaxValueByPoll: state => poll => {
    let l = state.pollToLvlLine[poll].length
    if (l) {
      return state.pollToLvlLine[poll][l - 1]
    } else {
      return 0
    }
  },
  // /**
  //  * @desc 
  //  * @param {Number} num 
  //  * @param {String} poll 
  //  * @returns {String} 
  //  */
  getLevelByNumber: state => ({num, poll} = {}) => {
    num = parseFloat(num)
    if (!num && num !== 0) {
      return '-'
    }
    if (!poll || !(state.pollToLvlLine[poll] && state.pollToLvlLine[poll].length)) {
      return '-'
    }
    let l = state.arr.length - 1
    for (let i = 0, l2 = state.pollToLvlLine[poll].length; i < l2; i++) {
      if (num <= state.pollToLvlLine[poll][i]) {
        return i > l ? state.arr[l] : state.arr[i]
      }
    }
    return state.arr[l]
  }
}

export default {
  namespaced: true,
  state,
  getters
}



