const state = {
  // 
  arr: [
    'aqi',
    'pm25',
    'pm10',
    'co',
    'no2',
    'so2',
    'o3',
    'o3_8h'
  ],
  // pollToShow: {
  //   aqi: 'AQI',
  //   pm25: 'PM2.5',
  //   pm10: 'PM10',
  //   co: 'CO',
  //   no2: 'NO2',
  //   so2: 'SO2',
  //   o3: 'O3',
  //   o3_8h: 'O3_8h'
  // },
  //  = 
  pollToShow: {
    aqi: 'AQI',
    pm25: 'PM.',
    pm10: 'PM',
    so2: 'SO',
    o3: 'O',
    o3_8h: 'O_8h',
    o38h: 'O8h',
    co: 'CO',
    no2: 'NO',
    nox: 'NO',
    tvoc: 'TVOC',
    bc: 'BC',
    oc: 'OC',
    voc: 'VOCs',
    nh3: 'NH'
  },
  //  = 
  pollToUnit: {
    aqi: 'g/m',
    pm25: 'g/m',
    pm10: 'g/m',
    co: 'mg/m',
    no2: 'g/m',
    so2: 'g/m',
    o3: 'g/m',
    o3_8h: 'g/m'
  }
}

const getters = {
  // /**
  //  * @desc 
  //  * @param {String} poll 
  //  * @returns {String} 
  //  */
  getShowByPoll: state => poll => state.pollToShow[poll],
  // /**
  //  * @desc 
  //  * @param {String} poll 
  //  * @returns {String} 
  //  */
  getUnitByPoll: state => poll => state.pollToUnit[poll]
}

export default {
  namespaced: true,
  state,
  getters
}



