<!--
 /**
  * @desc index.vue
  * @module @/components
  * @desc 
  * @author zkhq
  * @date 2020/3/30 10:09
  * @param {Object} [title]    - 
  * @param {String} [columns] - 
  * @example 
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    width="480px"
  >
    <el-form :model="ruleForm" :rules="rules" label-suffix=":" ref="ruleForm" label-width="120px" class="demo-ruleForm">
      <el-form-item label="" prop="passwordOld">
        <el-input type="password" v-model="ruleForm.passwordOld" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="passwordNew">
        <el-badge :hidden="badgeHidden" :value="badgeValue" :type="badgeInfo">
          <el-input type="password" v-model="ruleForm.passwordNew" placeholder=""></el-input>
        </el-badge>
      </el-form-item>
      <el-form-item label="" prop="passwordAgain">
        <el-input type="password" v-model="ruleForm.passwordAgain" placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
        <el-button @click="close"  > </el-button>
        <el-button type="primary" @click="save" > </el-button>
      </span>
  </el-dialog>
</template>

<script>
// import
export default {
  name: 'PasswordChange',
  data () {
    let checkPasswordAgain = (rule, value, callback) => {
      if (value !== this.ruleForm.passwordNew) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      // data 
      //  //
      // 
      dialogVisible: false,
      // 
      ruleForm: {
        passwordOld: '',
        passwordNew: '',
        passwordAgain: ''
      },
      // 
      rules: {
        passwordOld: [
          {required: true, message: '', trigger: 'blur'}
        ],
        passwordNew: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, message: ' 6', trigger: 'blur'}
        ],
        passwordAgain: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkPasswordAgain, trigger: 'blur'}
        ]
      }
    }
  },
  props: {
    // visible: {
    //   default: false,
    //   type: Boolean
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
  },
  destroyed () {
  },
  watch: {},
  computed: {
    passwordStrong () {
      return this.$common.analyzePasswordSecurityLevel(this.ruleForm.passwordNew)
    },
    badgeHidden () {
      return this.$common.objC(this.ruleForm, 'passwordNew').length < 6
    },
    badgeValue () {
      let dict = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: ''
      }
      return `${dict[this.passwordStrong]}`
    },
    badgeInfo () {
      let dict = {
        0: 'info',
        1: 'danger',
        2: 'warning',
        3: 'primary',
        4: 'success'
      }
      return dict[this.passwordStrong]
    }
  },
  directives: {},
  filter: {},
  methods: {
    // 
    //  /** */
    /**
     * @desc 
     */
    open () {
      this.dialogVisible = true
    },
    close () {
      this.dialogVisible = false
    },
    save () {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.change()
        } else {
          return false
        }
      })
    },
    change () {
      this.$api.modifyPwd({
        newpwd: this.ruleForm.passwordNew,
        oldpwd: this.ruleForm.passwordOld
      }).then((respone) => {
        if (respone.data.code === 0) {
          this.$message({
            message: '',
            type: 'success'
          })
          this.close()
        } else {
          this.$message.error(respone.data.message)
        }
      }).catch((error) => {
        console.log(`%c${error} `, 'color:blue')
        this.$message.error(``)
      })
    }
  }
}
</script>

<style scoped>
</style>



