/**
 * @desc 
 */
export const apiTable = (data) => {
  const {pageSize, pageNumber} = data
  console.log(pageSize)
  const arr = [
    {
      'date': '2016-05-02',
      'name': '',
      'address': ' 1518 '
    },
    {
      'date': '2016-05-04',
      'name': '',
      'address': ' 1517 '
    },
    {
      'date': '2016-05-01',
      'name': '',
      'address': ' 1519 '
    },
    {
      'date': '2016-05-03',
      'name': '',
      'address': ' 1516 '
    },
    {
      'date': '2016-05-03',
      'name': '',
      'address': ' 1515 '
    }
  ]
  return Promise.resolve({
    data: {
      code: 0,
      data: {
        total: arr.length,
        content: arr.slice((pageNumber - 1) * pageSize, pageNumber * pageSize)
      }
      // data: data
    }
  })
}



