import ajax from '@/utils/http'
const ahead = '/api/upms/'
// 
/**
 * @desc 
 */
export const getVerification = (data) => ajax({
  url: ahead + 'sso/queryVerificationCode',
  method: 'post',
  data,
  responseType: 'json'
})

