// import {apiArea} from '@/api'
import {objC} from '@/utils/common'

const state = {
  // 
  arr: [],
  //  code = 
  areaCodeToObj: {},
  // 
  areaDefault: '110000',
  // 
  areaLevelArr: [
    'provinces',
    'city',
    'county',
    'street'
  ],
  //  = 
  areaLevelNumberToAreaLevel: {
    '0': 'provinces',
    '1': 'provinces',
    '2': 'city',
    '3': 'county',
    '4': 'street'
  },
  //  = 
  areaLevelNumberToGroupAreaLevel: {
    '0': 'provinces',
    '1': 'city',
    '2': 'county',
    '3': 'street',
    '4': 'street'
  },
  //  = 
  areaLevelToShow: {
    provinces: '',
    city: '',
    county: '',
    street: ''
  },
  //  = 
  areaToZoom: {
    provinces: 8,
    city: 10,
    county: 12,
    street: 14
  }
}

const getters = {
  // /**
  //  * @desc 
  //  * @returns {string} 
  //  */
  areaNameByAreaDefault: (state) => objC(state.areaCodeToObj[state.areaDefault], 'name'),
  // /**
  //  * @desc 
  //  * @returns {string} 
  //  */
  areaLevelByAreaDefault: (state, getters) => getters.getAreaLevelByCode(state.areaDefault),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {string|Number} 
  //  */
  getAreaLevelNumberByCode: (state) => (areaCode = state.areaDefault) => objC(state.areaCodeToObj[areaCode], 'level'),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {string} 
  //  */
  getAreaNameByCode: (state) => ({areaCode = '', defaultName = ''} = {}) => objC(state.areaCodeToObj[areaCode], 'name') || defaultName,
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {string} 
  //  */
  getAreaLevelByCode: (state, getters) => (areaCode = state.areaDefault) => objC(state.areaLevelNumberToAreaLevel, getters.getAreaLevelNumberByCode(areaCode)),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {string} 
  //  */
  getGroupAreaLevelByCode: (state, getters) => (areaCode = state.areaDefault) => objC(state.areaLevelNumberToGroupAreaLevel, getters.getAreaLevelNumberByCode(areaCode)),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {Number} 
  //  */
  getZoomByCode: (state, getters) => (areaCode = state.areaDefault) => objC(state.areaToZoom, getters.getAreaLevelByCode(areaCode)),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {String} 
  //  */
  getLevelNameByCode: (state, getters) => (areaCode = state.areaDefault) => objC(state.areaLevelToShow, getters.getAreaLevelByCode(areaCode)),
  // /**
  //  * @desc  code  code
  //  * @param {String|Number} areaCode  code
  //  * @returns {Number}  code
  //  */
  getParentCodeByCode: (state) => (areaCode) => objC(state.areaCodeToObj[areaCode], 'parent'),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {Number} 
  //  */
  getParentNameByCode: (state, getters) => (areaCode) => getters.getAreaNameByCode(getters.getParentCodeByCode(areaCode)),
  // /**
  //  * @desc  code 
  //  * @param {String|Number} areaCode  code
  //  * @returns {Array} 
  //  */
  getChildsByCode: (state) => (areaCode = state.areaDefault) => {
    //  code  String|Number String
    //  code   Array
    areaCode = String(areaCode)
    let areas = areaCode.split(',')
    let areaDict = {}
    for (let i = 0, l = areas.length; i < l; i++) {
      let area = areas[i]
      areaDict[area] = true
    }
    // 
    let arr = []
    for (let i = 0, l = state.arr.length; i < l; i++) {
      let parent = String(state.arr[i].parent)
      let code = String(state.arr[i].code)
      if (areaDict[parent]) {
        arr.push(code)
      }
    }
    return arr
  }
}

